@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,400&family=Playfair+Display:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiTableCell-root {
  font-family: 'Montserrat', sans-serif !important;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.header-title {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: calc(10px + 4.5vmin);
  margin: auto;
  margin-top: 2%;
  padding: 15px;
  width: 80%;
}

.logo {
  margin-top: 1%;
  width: calc(60px + 8vmin);
  border-radius: 100px / 120px;
  opacity: 0.8;
}

.rule-header h2 {
    font-family: 'Playfair Display', serif;
}

.rule-box {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    padding: 0 15px 15px 15px;
}

.rule-box p {
    text-align: justify;
}

.rule-header {
    color: white;
    background-color: black;
    text-align: left;
    padding-left: 10px;
}

p.eksempel, span.eksempel {
    font-style: italic;
}

.emphasis {
    font-weight: 500;
}

table {
    table-layout: fixed;
    width: 100%;
}

/**************
Book reference
***************/

p #title {
    font-style: italic;
    text-decoration: underline;
    color: lightseagreen
}

p #year {
    color: lightslategray;
}

.quote::before {
    content: open-quote;
}

.quote::after {
    content: close-quote;
}

.quote {
    quotes: "“" "”" "‘" "’";
}



@media only screen and (max-width: 600px) {
    table {
        word-break: break-word;
    }
  }
